.projects__item {
  border-radius: 20px;
  background: #fff;
  /* Shadow Blue 01 */
  box-shadow: 0px 20px 20px 0px rgba(0, 63, 128, 0.05);
  padding: 10px;
  margin-bottom: 40px;
  color: var(--blue-blue-03);

  &-image {
    width: 830px;
    height: 299px;
  }

  &-info {
    padding: 40px;
  }
}

.apartament__count {
  border-radius: 30px;
  background: var($--blue-blue-01);
  
}

@media (min-width: 500) {
  .projects__item {
    display: block;
  } 
}
