*,
*::before,
*::after {
  box-sizing: border-box;
}

.flex {
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
}

.--just-space {
  justify-content: space-between;
}

.--just-center {
  justify-content: center;
}

.--just-end {
  justify-content: flex-end;
}

.--align-str {
  align-items: stretch;
}

.--align-center {
  align-items: center;
}

.--align-end {
  align-items: flex-end;
}

.--dir-col {
  flex-direction: column;
}

// переменые

$--blue-blue-01: #F2F9FF;

$--gradient-blue-gradient: linear-gradient(180deg, #409fff 0%, #007fff 100%);

$--blue-blue-02: #007fff;

$--blue-blue-03: #002040;

.--body-medium {
  font-size: 16px;
  font-style: normal;
  font-weight: 500;
  line-height: normal;
}

.--subtitle-bold {
  /* Subtitle/Desktop/Subtitle Bold */
  font-size: 18px;
  font-style: normal;
  font-weight: 700;
  line-height: normal;
}

--h2 {
  /* Heading/Desktop/H2 */
font-size: 24px;
font-style: normal;
font-weight: 600;
line-height: normal;
}

html {
  font-size: 16px;
}

html,
body {
  min-height: 100%;
  position: relative;
}

body {
  font-family: "Montserrat", sans-serif;
}

ul,
ol,
li,
p,
h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
}

ul,
li {
  padding: 0;
  margin: 0;
  list-style: none;
}

a {
  text-decoration: none;
}

.container {
  width: 1300px;
  margin: 0 auto;
  padding: 0 15px;
}

@media  (min-width: 500px) {
  .container-m {
    width: 480px;
  } 
}

.--button {
  background: var(
    --gradient-blue-gradient,
    linear-gradient(180deg, #409fff 0%, #007fff 100%)
  );
  /* Button Effects */
  box-shadow: 0px -2px 0px 0px rgba(0, 32, 64, 0.1) inset,
    0px 2px 0px 0px rgba(255, 255, 255, 0.25) inset,
    0px 10px 10px 0px rgba(0, 32, 64, 0.1);
  border: none;
  cursor: pointer;

  &:hover {
    background: var(--blue-blue-02);
    /* Button Effects */
    box-shadow: 0px -2px 0px 0px rgba(0, 32, 64, 0.1) inset,
      0px 2px 0px 0px rgba(255, 255, 255, 0.25) inset,
      0px 10px 10px 0px rgba(0, 32, 64, 0.1);
  }
}
